* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.disclaimer_container {
  width: 100%;
  height: 880px;
  display: flex;
  justify-content: center;
  align-items: center;

  .disclaimer_inner {
    height: 90%;
    width: 60%;
    border-radius: 5px;
    padding: 10px;
    text-align: center;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

  }
}